@import '../../../../styles/colors.scss';
@import '../../../../styles/mixins.scss';
@import '../../../../styles/variables.scss';
.history-login-container {
  margin-top: 45px;
  .history-login-header-container{
    padding:0px;
    .history-login-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height:61px;
      margin-left: 20px;
      margin-right: 10px;
      .history-login-header__left {
        @include text-setup($charcoal-grey, 16px);
        font-weight: 600;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: 0.4px;
        text-align: left;
      }
      .history-login-header__right {
        @include text-setup($dark-sky-blue, 15px);
        font-weight: bold;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: 0.4px;
        text-align: right;
        width: 110px;
        height: 40px;
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }

}

.all-history-login {
  margin-top: 0px !important;
  .history-login-header-container {
    display: none !important;
  }
}

.history-list-container{
  display: flex;
  flex-flow: column;
  width: 100%;
  padding-bottom:5px;
  .history-list{
    border-top: solid 0.5px #dedede;
    padding-bottom: 10px;
    .history-list__title {
      @include text-setup($charcoal-grey, 13px);
      font-weight: 600;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 0.3px;
      margin-top: 11px;
      margin-bottom: 3px;
    }

    .history-list__name{
      @include text-setup($charcoal-grey, 13px);
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: 0.3px;
    }
  }
}

.history-login-list-container-desktop {
  display: none;
}

@include media-query(md) {

  .history-login-container{
    margin-top:30px !important;
    .history-login-header{
      height:63px !important;
      margin-left:30px !important;
      margin-right:0px !important;
      .history-login-header__right {
        height: 48px !important;
        width: 151px !important;
      }
    }
  }
  .history-login-list-container {
    display: none;
  }
  .history-login-list-container-desktop {
    display: flex !important;
    justify-content: center;
    .history-login-list-table{
      width:100%;
      thead{
        tr{
          height: 48px;
          background-color: #f8f8f8;
          font-family: NunitoSans;
          font-size: 13px;
          font-weight: 600;
          font-style: normal;
          font-stretch: normal;
          line-height: normal;
          letter-spacing: 0.3px;
          color: #404852;
          .history-login-list-table__header-column:nth-child(1){
            padding-left:30px;
          }
        }
      }
      tbody{
        tr.history-login-list-body__item{
          height: 45px;
          background-color: #ffffff;
          background-color: var(--white);
          border-top: solid 0.5px #dedede;
          font-family: NunitoSans;
          font-size: 13px;
          font-weight: normal;
          font-style: normal;
          font-stretch: normal;
          line-height: normal;
          letter-spacing: 0.3px;
          color: #404852;
          .history-login-list-table__body-column-date{
            width: 135px;
            padding-left:30px;
            padding-right: 35px;
          }
          .history-login-list-table__body-column-country{
            width: 59px;
            padding-right: 106px;
          }
          .history-login-list-table__body-column-ip{
            width: 92px;
            padding-right: 111px;
          }
          .history-login-list-table__body-column-browser{

          }
        }
      }
    }
  }

  .all-history-login {
    margin-top: 7px !important;
    .history-login-header-container {
      display: block !important;
      .history-login-header__right {
        display: none !important;
      }
    }
  }
}