@import '../../../styles/colors.scss';
@import '../../../styles/mixins.scss';
@import '../../../styles/variables.scss';

#profile-card {
  .profile-card-container{
    min-height: calc(100vh - 50px);
    .profile-card-content-container{
      padding: 0px;
    }
  }

  .profile-container {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    flex-flow: column;
    .profile-settings-icon-container{
      position: absolute;
      top: 10px;
      right: 2px;
    }
    .divider{
      width: 165px;
      height: 2px;
      border-top: solid 0.5px #dedede;
    }
  }

  .profile-nav-container {
    margin-top: 30px;
    .profile-nav-list-container {
      padding: 0px;
      .profile-nav-list-item {
        padding:0px;
        height:55px;
        border-top: solid 0.5px #dedede;
      }
    }
  }

}


.custom-divider{
  width: 100%;
  height:2px;
  border-top: solid 0.5px #dedede;
}


@include media-query(sm) {
  .profile-settings-icon {
    margin-right: 0px !important;
  }
}

.profile-info {
  text-align: center;
  margin-top: 40px;
  .profile-info__img {
    height: 60px;
    width: 60px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    cursor: pointer;
    margin: 0 auto;
    border-radius: 50px;
    img {
      width: 60px;
      height: 60px;
      border-radius: 50px;
      vertical-align: top;
      cursor: pointer;
    }
  }
  .profile-info__name {
    margin-top: 15px;
    @include text-setup($charcoal-grey, 16px);
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 0.6px;
    font-weight: 600;
  }
  .profile-info__email {
    margin-top: 5px;
    @include text-setup($charcoal-grey, 13px);
    letter-spacing: 0.6px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 0.5px;
  }
  .divider {
    margin-top: 15px;
  }
}
.balance-info {
  text-align: center;
  margin-top: 15px;
  .balance-info__text {
    @include text-setup($steel-grey, 14px);
    font-weight: 600;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 0.5px;
  }
  .balance-info__amount {
    @include text-setup($charcoal-grey, 22px);
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 1px;
    margin-top: 3px;
  }
}

.profile-buttons-container {
  margin-top:20px;
}

.profile-nav-btn{
  display: flex;
  align-items: center;
  margin-left: 20px;
  .profile-nav-btn__text{
    font-weight: normal;
    @include text-setup($steel-grey,15px);
    letter-spacing: 0.5px;
    margin-top: 2px;
  }
  .profile-nav-btn__navicon{
    position: absolute;
    right:15px;
  }
}

@include media-query(md) {
  #profile-card{
    .profile-card-container {
      height: 499px !important;
      min-height: 499px !important;
      .profile-card-content-container {
        padding: 0px;
      }
      .profile-container {
        margin-top: 0px !important;
        .profile-settings-icon-container {
          position: absolute;
          top: 7px !important;
          right: 7px !important;
          height: 40px;
          width: 40px;
        }
        .profile-info {
          margin-top: 30px;
          .profile-info__name {}
          .profile-info__img {
            height: 60px;
          }
        }
      }
    }
  }

  .custom-divider{
    display: none;
  }

  .profile-buttons-container{
    display: flex !important;
    align-items: center;
    margin-left:-20px
  }

  .dot-desktop{
    width: 6px;
    height: 6px;
    background-color: $dark-sky-blue;
    border-radius: 20px;
  }

  .profile-nav-container {
    margin-top: 10px;

    .profile-nav-list-container .profile-nav-list-item {
      height:48px !important;
      border-top:0px !important;
    }
    .profile-nav-btn {
      padding: 0px;
      // margin-left:-4px;
      .profile-nav-btn__icon{
        padding:0px;
        margin-right: 15px !important;
      }
      .profile-nav-btn__text{
        @include text-setup($steel-grey, 13px);
        font-weight: normal;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: 0.5px;
      }
      .profile-nav-btn__navicon {
        display: none;
      }
    }
    .profile-nav-btn-active{
      border-left:3px solid $dark-sky-blue;
      .profile-nav-btn__text{
        font-weight: bold;
        color: $dark-sky-blue;
      }
    }
  }

}

.custom-tooltip-profile {
  font-family: $font-family !important;
  font-size: 13px !important;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0.3px;
  color: rgb(255, 255, 255) !important;
  background-color: rgb(64, 72, 82) !important;
  opacity: 1 !important;
  top: 126px !important;
  padding: 0px;
  width: 115.6px;
  height: 39px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.custom-tooltip-profile:after {
  border-left-color: #404852 !important;
  border-left-style: solid !important;
  border-left-width: 6px !important;
}